import {Link} from "react-router-dom";
import './Menu.css';

function Menu() {
    return(
    <div className="menu">
        <ul className="menu-partes"><Link to="/">Inicio</Link></ul>
        <ul className="menu-partes"><Link to="/registro">Registro</Link></ul>
        <ul className="menu-partes"><Link to="/login">Login</Link></ul>
    </div>
    )
}

export default Menu;